.recipeBoxContainer {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
}


.recipeBox {
  width: 200px;
  height: 320px; 
  box-shadow: 0 3px 10px #aaa;
  text-align: center;
  padding: 15px;
  border-radius: 8px;
  margin: 14px;
  overflow-wrap: break-word; 
  max-height: 330px; 
}


.recipeBox img {
  width: 170px;
  height: 123px;
  max-width: 100%; 
  max-height: 90%; 
  margin-bottom: 10px;
}

.recipeDescription {
  border-top: 2px solid #e2ebe4;
  margin-top: 10px;
  padding-top: 5px;
}

.recipeDescription span {
  display: block;
  margin-top: 10px;
}

.recipeDescription span:first-child {
  font-weight: bold;

}

.recipeDescription span:nth-child(2) {

  border: 2px solid #dc3545;
  color: #dc3545;
  padding: 5px;
  margin-top: 25px;
  border-radius: 5px;
  cursor: pointer;
}

.recipeDescription span:nth-child(3) {
  border: 2px solid #2ea56a;
  color: #185747;
  padding: 5px;
  margin-top: 8px;
  border-radius: 5px;
  cursor: pointer;
}
button {
  background-color: #dc3545;
  color: #fff;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

button:hover {
  background-color: #c82333;
}

