.recipelistcomponent {
  display: flex;
  flex-direction: row;
  padding: 30px;
  justify-content: space-evenly;
}

.recipeContainer {
  background-color: #fff; /* Set your desired background color */
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Box shadow for a subtle effect */
  padding: 20px;
  text-align: center;
}


.recipeBox img {
  height: 130px;
  margin-bottom: 10px;
}

.recipeContainer span {
  display: block;
  margin-top: 10px;
}

.recipeContainer span:first-child {
  font-weight: bold;
  font-size: 12px;
}



.recipeContainer span:nth-child(2) {
  color: #007bff; /* Set your desired color for the "Save Recipe" text */
  cursor: pointer;
}

.recipeContainer span:nth-child(2):hover {
  text-decoration: underline;
}
